/*********************************************************/
/*                 Layout Styles                         */
/*********************************************************/
/* ==========================================================================
   Sticky footer styles
   ========================================================================== */
html {
  position: relative;
  min-height: 100%;
}
body {
  /* Margin bottom by footer height */
  /* margin-bottom: 48px; */
}
.Footer,
#footer {
  position: absolute;
  /* bottom: 0; */
  width: 100%;
  /* Set the fixed height of the footer here */
  /* height: 48px; */
}
/* END Sticky footer styles
   ========================================================================== */
/*********************************************************/
/*                 Template Layout                       */
/*********************************************************/
#wrap:before,
#top:before,
.head:before,
.Footer:before,
#wrap:after,
#top:after,
.head:after,
.Footer:after {
  content: ' ';
  display: table;
}
#wrap:after,
#top:after,
.head:after,
.Footer:after {
  clear: both;
}
.Wrap,
#wrap {
  margin: 0 auto;
  position: relative;
}
#left {
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  width: 250px;
  z-index: 999;
}
#left {
  position: absolute;
  left: -250px;
}
.sidebar-left-opened #left {
  left: 0;
}
@media (min-width: 768px) {
  #left {
    position: -webkit-sticky;
    position: sticky;
    float: left;
    left: auto;
  }
  .sidebar-left-hidden #left {
    width: 0;
    visibility: hidden;
  }
  .sidebar-left-mini #left {
    width: 50px;
  }
  #content {
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    overflow: hidden;
  }
}
@media (min-width: 1200px) {
  body.boxed {
    margin-bottom: 0;
  }
  body.boxed .Wrap,
  body.boxed #wrap,
  body.boxed .Footer,
  body.boxed #top .navbar.navbar-fixed-top {
    max-width: 1170px;
    margin-left: auto;
    margin-right: auto;
  }
  body.boxed .Footer,
  body.boxed #footer {
    position: relative;
  }
}
/* ================== END Template Layout ============== */
/*********************************************************/
/*                 right onoffcanvas                    */
/*********************************************************/
#right {
  width: 250px;
  overflow-y: auto;
  padding: 1rem;
}
/* BEGIN TOP bar */
#top .navbar {
  margin-bottom: 0;
  border-top: 3px solid #cd0067;
}
#top .navbar-brand {
  padding: 0;
}
#top .topnav {
  margin: 10px 0;
  text-align: right;
}
@media (min-width: 768px) {
  #top .topnav {
    float: right;
  }
}
/* END TOP bar */
/* BEGIN header.head bar */
.head {
  border-bottom: 1px solid rgba(0, 0, 0, 0.3);
  box-shadow: 0px 1px 0px rgba(255, 255, 255, 0.05) inset;
  text-shadow: 0px 1px 0px rgba(0, 0, 0, 0.5);
}
.head .search-bar {
  margin: 0 auto;
  padding: 10px 15px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.3);
  box-shadow: 0 1px 0 rgba(255, 255, 255, 0.05);
}
.head .search-bar .main-search {
  border-bottom: 1px solid #7c7c7c;
}
.head .search-bar .main-search input[type='text'] {
  background-color: transparent;
  border-color: transparent;
  color: #ccc;
  box-shadow: none;
}
.head .search-bar .main-search button,
.head .search-bar .main-search input[type='submit'] {
  background: transparent;
  border: transparent;
}
@media (min-width: 768px) {
  .head .search-bar {
    border-bottom-width: 0;
    border-right: 1px solid rgba(0, 0, 0, 0.3);
    box-shadow: -1px 0px 0px rgba(255, 255, 255, 0.05) inset;
  }
  .side-right .head .search-bar {
    border-right-width: 0;
    border-left: 1px solid rgba(0, 0, 0, 0.3);
    box-shadow: 1px 0px 0px rgba(255, 255, 255, 0.05) inset;
  }
}
.head .main-bar {
  padding: 10px;
}
.head .main-bar h3 {
  margin-top: 0;
  margin-bottom: 0;
  color: #bababa;
}
@media (min-width: 768px) {
  .head .main-bar h3 {
    margin-top: 5px;
  }
}
@media (min-width: 768px) {
  .head .search-bar .main-search {
    margin-right: auto;
  }
}
@media (min-width: 768px) {
  .search-bar {
    float: left;
    padding: 10px 4px;
  }
  .side-right .search-bar {
    float: right;
  }
  .search-bar .input-small.form-control {
    border-radius: 3px;
  }
  .main-bar {
    display: block;
    overflow: hidden;
  }
}
@media (min-width: 992px) {
  .search-bar {
    width: 250px;
    padding: 10px 15px;
  }
  .search-bar .input-group-btn {
    display: table-cell;
  }
  .search-bar .input-small.form-control {
    border-radius: 3px 0 0 3px;
  }
  .mini-sidebar .search-bar .input-small.form-control {
    border-radius: 3px;
  }
}
/** END header.head bar */
/*********************************************************/
/*          Begin LEFT Styles                           */
/*********************************************************/
.user-media .user-link {
  display: block;
  margin-left: auto;
  margin-right: auto;
  position: relative;
}
.user-media .user-link .user-img {
  margin: 10px auto;
}
.user-media .user-link .user-label {
  position: absolute;
  top: -6px;
  right: 6px;
}
.user-media .user-link {
  float: left;
}
.user-media .user-link .user-img {
  margin: 10px;
}
.user-media .user-link .user-label {
  top: 6px;
  right: 0;
}
.user-media .media-body {
  margin-left: 12px;
}
.user-media .media-body h5.media-heading {
  color: #fff;
  padding-top: 8px;
  margin-bottom: 3px;
}
.user-media .media-body ul.user-info li {
  color: #ccc;
}
.user-media:before,
.user-media:after {
  content: ' ';
  display: table;
}
.user-media:after {
  clear: both;
}
.side-right .user-link .user-label {
  left: 6px;
  right: auto;
}
.sidebar-left-mini .user-wrapper {
  position: absolute;
  top: 0;
  left: -100%;
  display: none;
  min-width: 250px;
}
.user-media-toggleHover {
  display: none;
}
.sidebar-left-mini .user-media-toggleHover {
  font-size: 18px;
  color: #fff;
  padding: 10px;
  text-align: center;
  cursor: pointer;
  display: inherit;
}
.sidebar-left-mini .user-media:hover .user-wrapper {
  left: 100%;
  display: inherit;
  z-index: 9999;
}
.sidebar-left-mini.side-right .user-media:hover .user-wrapper {
  left: auto;
  right: 100%;
}
/* ============== END LEFT Styles ================= */
#menu,
#menu ul,
#menu li,
#menu a {
  padding: 0;
  margin: 0;
  color: #f4f4f4;
}
#menu,
#menu ul {
  list-style: none;
}
#menu li,
#menu a {
  position: relative;
  display: block;
}
#menu a,
#menu a:hover,
#menu a:focus,
#menu a:active {
  text-decoration: none;
}
#menu ul {
  background: rgba(0, 0, 0, 0.3);
}
#menu > li > a {
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.05);
  text-shadow: 0 1px 0 rgba(0, 0, 0, 0.5);
  padding: 10px 0 10px 15px;
}
#menu > li.active > a {
  background-color: rgba(42, 63, 84, 1);
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.1) inset;
}
#menu > li:hover > a,
#menu > li:focus > a {
  background-color: rgba(0, 0, 0, 0.1);
  outline: none;
  box-shadow: none;
}
#menu > li ul {
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.05);
  border-left-width: 0;
}
#menu > li ul a {
  padding: 11px 0 11px 30px;
  font-size: 13px;
}
#menu > li ul a:hover {
  background-color: rgba(0, 0, 0, 0.1);
}
@media (min-width: 768px) {
  .sidebar-left-mini #menu > li > ul {
    position: absolute;
    top: 0;
    left: 100%;
    box-shadow: none;
    min-width: 200px;
    z-index: 1;
    background-color: rgba(0, 0, 0, 0.8);
    display: none;
    top: 100%;
  }
  .sidebar-left-mini #menu > li:hover > ul {
    display: inherit;
    visibility: visible;
  }
}
@media (min-width: 768px) {
  .sidebar-left-mini #menu > li > a .fa {
    font-size: 18px;
  }
  .sidebar-left-mini #menu > li > a .fa.arrow {
    display: none;
  }
  .sidebar-left-mini #menu > li > a .link-title {
    border-top: 1px solid rgba(0, 0, 0, 0.1);
    border-right: 1px solid rgba(0, 0, 0, 0.1);
    box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.05);
    position: absolute;
    left: 100%;
    top: 0;
    z-index: 1;
    background: rgba(0, 0, 0, 0.85);
    display: none;
    padding: 10px 30px;
  }
  .sidebar-left-mini #menu > li:hover > a > .link-title {
    display: block;
    min-width: 200px;
  }
}
body:not(.sidebar-left-mini) #menu .nav-header {
  padding: 3px 15px !important;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 12px;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  box-shadow: 0px 1px 0px rgba(255, 255, 255, 0.05) inset;
  text-shadow: 0px 1px 0px rgba(0, 0, 0, 0.5);
  background: rgba(0, 0, 0, 0.1);
}
#menu .nav-divider {
  height: 1rem;
  margin: 0;
  background: rgba(0, 0, 0, 0.2);
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.1) inset;
}
.sidebar-left-mini #menu .nav-header {
  position: absolute !important;
  overflow: hidden !important;
  width: 1px !important;
  height: 1px !important;
  padding: 0 !important;
  border: 0 !important;
  clip: rect(1px, 1px, 1px, 1px) !important;
}
#menu .fa.arrow {
  float: right;
  padding-top: 3px;
  margin-right: 15px;
}
#menu .fa.arrow:before {
  content: '\F104';
}
#menu .active > a > .fa.arrow:before {
  content: '\F107';
}
/* BEGIN CONTENT STYLES */
#content {
  -webkit-transition: width 0.4s;
  transition: width 0.4s;
}
.outer {
  padding: 10px;
  background-color: #6e6e6e;
}
.outer:before,
.outer:after {
  content: ' ';
  display: table;
}
.outer:after {
  clear: both;
}
.inner {
  position: relative;
  min-height: 1px;
  padding-right: 10px;
  padding-left: 10px;
  background: #fff;
  border: 10px solid #e4e4e4;
}
@media (min-width: 768px) {
  .inner {
    float: left;
    width: 100%;
  }
}
/* END CONTENT STYLES */
/* ==========================================================================
   Footer
   ========================================================================== */
.Footer,
#footer {
  margin: 0 auto;
  color: #f4f4f4;
  border-top: 1px solid #222;
}
.Footer p,
#footer p {
  padding: 1.2rem 0;
  margin: 0;
  text-align: center;
}
/* END Footer
   ========================================================================== */
