.range-slider {
  -webkit-appearance: none;
  width: 100%;
  height: 3px;
  border-radius: 0px;
  background: #ffffff;
  outline: none;
  opacity: 1;
  -webkit-transition: 0.2s;
  -webkit-transition: opacity 0.2s;
  transition: opacity 0.2s;
}

.range-slider:hover {
  opacity: 1;
}

.range-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #1baa92;
  cursor: pointer;
}

.range-slider::-moz-range-thumb {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #1baa92;
  cursor: pointer;
}
.range-slider {
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(#1baa92),
    color-stop(0%, #1baa92),
    color-stop(0%, #fff),
    to(#fff)
  );
  background: linear-gradient(
    to right,
    #1baa92 0%,
    #1baa92 0%,
    #fff 0%,
    #fff 100%
  );

  outline: none;
  -webkit-transition: background 450ms ease-in;
  transition: background 450ms ease-in;
  -webkit-appearance: none;
}

