body {
  font-family: -apple-system, system-ui, BlinkMacSystemFont, 'Segoe UI', Roboto,
    'Helvetica Neue', Arial, sans-serif;
}
/*********************************************************/
/*                 Layout Styles                         */
/*********************************************************/
/* ==========================================================================
   Sticky footer styles
   ========================================================================== */
html {
  position: relative;
  min-height: 100%;
}
body {
  /* Margin bottom by footer height */
  /* margin-bottom: 48px; */
}
.Footer,
#footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  /* Set the fixed height of the footer here */
  /* height: 48px; */
}
/* END Sticky footer styles
   ========================================================================== */
/*********************************************************/
/*                 Template Layout                       */
/*********************************************************/
#wrap:before,
#top:before,
.head:before,
.Footer:before,
#wrap:after,
#top:after,
.head:after,
.Footer:after {
  content: ' ';
  display: table;
}
#wrap:after,
#top:after,
.head:after,
.Footer:after {
  clear: both;
}
.Wrap,
#wrap {
  margin: 0 auto;
  position: relative;
}
#left {
  transition: all 0.3s ease-in-out;
  width: 250px;
  z-index: 999;
}
#left {
  position: absolute;
  left: -250px;
}
.sidebar-left-opened #left {
  left: 0;
}
@media (min-width: 768px) {
  #left {
    position: -webkit-sticky;
    position: sticky;
    float: left;
    left: auto;
  }
  .sidebar-left-hidden #left {
    width: 0;
    visibility: hidden;
  }
  .sidebar-left-mini #left {
    width: 50px;
  }
  #content {
    transition: all 0.3s ease-in-out;
    overflow: hidden;
  }
}
@media (min-width: 1200px) {
  body.boxed {
    margin-bottom: 0;
  }
  body.boxed .Wrap,
  body.boxed #wrap,
  body.boxed .Footer,
  body.boxed #top .navbar.navbar-fixed-top {
    max-width: 1170px;
    margin-left: auto;
    margin-right: auto;
  }
  body.boxed .Footer,
  body.boxed #footer {
    position: relative;
  }
}
/* ================== END Template Layout ============== */
