.tooltip {
  position: relative;
  display: inline-block;
  opacity: 1;
  /* z-index: none; */
}

/* Tooltip text */
.tooltip .tooltiptext {
  visibility: hidden;
  background-color: grey;
  color: #fff;
  text-align: center;
  padding: 5px 5px;
  border-radius: 6px;
  top: 45px;
  /* Position the tooltip text - see examples below! */
  position: absolute;
  z-index: 9999;
  overflow: visible;
}

/* Show the tooltip text when you mouse over the tooltip container */
.tooltip:hover .tooltiptext {
  visibility: visible;
  overflow: visible;
  z-index: 9999;
}

